.contacts-form__fieldset {
  padding: 20px 20px 34px 20px;
  border: none;

  @media (min-width: @tablet-width) {
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.contacts-form__fieldset-wrapper {
  display: flex;
}

.contacts-form__fieldset-wrapper:not(:last-child) {
  margin: 0  0 16px 0;
}

.contacts-form__label {
  text-transform: uppercase;
  margin-left: 16px;
}

.contacts-form__input-tel {
  width: 222px;
  height: 58px;
  background-color: @input-color;
  border: none;
  padding-left: 16px;

  @media (min-width: @desktop-width) {
    width: 344px;
  }
}

.contacts-form__button-tel {
  width: 58px;
  height: 58px;
  background-color: @contact-button-color;
  border: none;
}

.contacts-form__input-email {
  width: 222px;
  height: 58px;
  background-color: @input-color;
  border: none;
  padding-left: 16px;

  @media (min-width: @desktop-width) {
    width: 344px;
  }
}

.contacts-form__button-email {
  width: 58px;
  height: 58px;
  background-color: @contact-button-color;
  border: none;
}
